<template>
  <a-breadcrumb>
    <a-breadcrumb-item v-if="$route.path !== '/'">
      <router-link to="/"> 首页 </router-link>
    </a-breadcrumb-item>
    <a-breadcrumb-item v-for="item in $route.matched" :key="item.meta.title">
      <router-link :to="item.path" v-if="item.path !== $route.path">
        {{ item.meta.title }}
      </router-link>
      <template v-else>
        {{ item.meta.title }}
      </template>
    </a-breadcrumb-item>
  </a-breadcrumb>
</template>
<script>
export default {};
</script>

<style lang="less">
.ant-breadcrumb a {
  font-size: 16px;
}

.ant-breadcrumb .ant-breadcrumb-link a:hover {
  color: #3553e8;
}
</style>
