/**
 * getOffset 获取距离浏览器的上左偏移量
 * @param {element} el 
 * @returns {t, l} 上左偏移量
 */
export const getOffset = function getOffset(el) {
    let t = el.offsetTop;
    let l = el.offsetLeft;
    let parent = el.offsetParent;
    while (parent) {
        t += parent.offsetTop;
        l += parent.offsetLeft;
        parent = parent.offsetParent;
    }
    return { t, l };
}


/**
 * debounce 防抖函数
 * @param {*} ctx this
 * @param {*} cb  callBack
 * @param {*} wait number 等待的间隔
 * @param {*} immediate boolean 是否立即执行
 * @returns cb执行的结果
 */
export const debounce = function debounce(ctx, cb, wait = 2000, immediate = true) {
    let timer = null,
        result = null;
    return function proxy(...args) {
        let now = immediate && !timer;
        clearTimeout(timer);
        timer = setTimeout(() => {
            !immediate ? result = cb.call(ctx, ...args) : null;
            timer = null;
        }, wait);
        now ? result = cb.call(ctx, ...args) : null;
        return result;
    }
}


/**
 * throttle节流函数，避免高频率触发回调函数
 * @param {this} ctx this
 * @param {Function} cb callBack
 * @param {number} wait number 等待的间隔
 * @returns cb执行的结果
 */
export const throttle = function throttle(ctx, cb, wait = 100) {
    let timer = null,
        per = 0,
        result;
    return function proxy(...args) {
        let now = new Date;
        if ((now - per) >= wait) {
            result = cb.call(ctx, ...args);
            clearTimeout(timer)
            timer = null;
            per = now;
        } else if (!timer) {
            timer = setTimeout(() => {
                clearTimeout(timer)
                timer = null;
                per = new Date;
                result = cb.call(ctx, ...args);
            }, wait);
        }
        return result;
    }
}

/* 跳转到指定的锚点 */
export const toTarget = function toTarget(to, vm) {
    if(!to.params.id) return;
    let id = "#" + to.params.id;
    vm.$nextTick(() => {
        let target = document.querySelector(id);
        let y = target.offsetTop - 72 - vm.$children[1].$el.offsetHeight;
        window.scrollTo(0, y);
    });
}