<template>
  <div class="educationOriented base">
    <!-- 视差指令 -->
    <div class="bg" v-parallax>
      <img src="/img/20211008201635.jpg" alt="" />
    </div>
    <EducationOrientedTitil></EducationOrientedTitil>
  </div>
</template>

<script>
import EducationOrientedTitil from "./educationOrientedTitil.vue";
import { getOffset } from "@utils";

export default {
  components: {
    EducationOrientedTitil,
  },
  directives: {
    parallax: {
      inserted(el, dirs, vnode) {
        let targetY = getOffset(el).t;
        el.scroll = (e) => {
          let winH = document.documentElement.scrollTop + window.innerHeight;
          let scrollY = winH - targetY; // 图片移动 =（滚动高度 + 一屏高度） - 目标元素距离浏览器的顶部的距离
          el.style.top = `-${scrollY * 0.1}px`;
        };
        // 监听滚动，当滚动时形成视觉差
        window.addEventListener("scroll", el.scroll, {
          passive: true,
        });
      },
      unbind(el, dirs, vnode) {
        // 移除事件
        window.removeEventListener("scroll", el.scroll, {
          passive: true,
        });
      },
    },
  },
};
</script>

<style lang="less" scoped>
.educationOriented {
  margin-top: 60px;
  max-width: 1920px;
  height: 600px;
  overflow: hidden;
  > .bg {
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
  }
}
@media screen and (max-width: 375px) {
.educationOriented {
  height: 500px;
}
}
</style>