  <template>
  <div class="seat" ref="seat">
    <div class="ls-scroll">
      <div class="ls-scroll-container" ref="scrollMenu">
        <div class="ls-scroll-menu" ref="clickMenu">
          <slot></slot>
        </div>
        <div class="ls-line" ref="line"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { getOffset, debounce } from "@utils";

export default {
  name: "ls-scroll-menu",
  props: {
    isFixed: {
      type: Boolean,
      default: true,
    },
    targetOffsetTop: {
      type: Number,
      default: 0.7,
    },
  },
  data() {
    return {
      debounce: debounce,
      isFirstClick: true, // 是否为首次点击
      pervTargetT: null, // 存储上一个目标元素距离浏览器的Y轴偏移量
      fristLoadEl: null, // 存储首次只是滚动的内容元素
      curKey: 0,
    };
  },
  computed: {
    seat() {
      return this.$refs.seat;
    },
    lineElm() {
      return this.$refs["line"];
    },
    scrollMenu() {
      return this.$refs["scrollMenu"];
    },
    selfT() {
      return getOffset(this.scrollMenu).t;
    },
    targetList() {
      let targets = [];
      this.$children.forEach((child) => {
        targets.push(document.querySelector(child.id));
      });
      return targets;
    },
  },
  mounted() {
    // 点击滚动
    this.$refs.clickMenu.addEventListener(
      "click",
      debounce(this, this.smoothScroll, 1000, true)
    );
    window.addEventListener("resize", this.followScroll);
  },
  activated() {
    // 滚动固定
    if (this.isFixed) {
      this.fixedContainer();
      window.addEventListener("scroll", this.fixedContainer, {
        passive: true,
      });
    }
    // 滚动跟随焦点
    this.followScroll();
    window.addEventListener("scroll", this.followScroll);
  },
  deactivated() {
    if (this.isFixed) {
      window.removeEventListener("scroll", this.fixedContainer, {
        passive: true,
      });
    }
    window.removeEventListener("scroll", this.followScroll);
  },
  beforeDestroy() {
    if (this.isFixed) {
      window.removeEventListener("scroll", this.fixedContainer, {
        passive: true,
      });
    }
    window.removeEventListener("resize", this.followScroll);
    window.removeEventListener("load", this.followScroll);
  },
  methods: {
    fixedContainer() {
      let targetT = document.documentElement.scrollTop;
      if (targetT > this.selfT - 72) {
        this.scrollMenu.style.position = "fixed";
        this.scrollMenu.style.top = "72px";
      } else {
        this.scrollMenu.style.position = "relative";
        this.scrollMenu.style.top = 0;
      }
    },
    smoothScroll(e) {
      let el = e.target;
      if (el.tagName === "A") {
        // 移除滚动跟随
        window.removeEventListener("scroll", this.followScroll);

        let isStop = false;
        let id = el.getAttribute("id");
        let doc = document.documentElement;
        let selfH = this.scrollMenu.scrollHeight;
        let target = document.querySelector(id);
        let targetT = getOffset(target).t;
        // 判断是否为首次点击
        this.isFirstClick
          ? this.fristLoadEl
            ? (this.pervTargetT = getOffset(this.fristLoadEl).t)
            : null
          : null;

        // 向上滚动
        let scrollUp = (winT, limitT) => {
          if (winT <= limitT) {
            clreanAnimation();
          } else {
            doc.scrollTop = winT - (winT - limitT) / 10;
            window.requestAnimationFrame(animationFn);
          }
        };

        // 向下滚动
        let scrollDown = (winT, limitT) => {
          if (winT >= limitT) {
            clreanAnimation();
          } else {
            // 当前位置 + (目标位置 - 当前位置) / 10 由快到慢的动画
            doc.scrollTop = winT + (targetT - winT) / 10;
            window.requestAnimationFrame(animationFn);
          }
        };

        // 清除动画
        let clreanAnimation = () => {
          if (!isStop) {
            this.pervTargetT = targetT;
            isStop = true;
          }
          window.cancelAnimationFrame(animationFn);
          // 添加滚动跟随
          this.followScroll();
          window.addEventListener("scroll", this.followScroll);
        };

        // 动画函数
        let animationFn = () => {
          let winT = doc.scrollTop;
          let limitT = targetT - (selfH + 72);
          if (targetT <= this.pervTargetT) {
            // targetT 小于 this.pervTargetT 那么就向上滚动
            scrollUp(winT, limitT);
          } else {
            scrollDown(winT, limitT);
          }
        };
        animationFn();
      }
    },
    followScroll() {
      this.targetList.forEach((target, key) => {
        let winH = document.documentElement.clientHeight * this.targetOffsetTop;
        let rect = target.getBoundingClientRect();
        let targetT = rect.top;
        if (winH > targetT) {
          this.curKey = key;
          let a = this.$children[key].$el.firstElementChild;
          let curX = a.offsetLeft - 10;
          this.lineElm.style.transform = `translateX(${curX}px)`;
          this.pervTargetT = getOffset(target).t;
          if (this.isFirstClick) {
            this.fristLoadEl = target;
          }
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.seat {
  /* 占位盒子 */
  width: 100%;
  height: 133px;
}

.ls-scroll-container {
  display: flex;
  padding: 50px 38px;
  width: 100%;
  flex-direction: column;
  background-color: #ffffff;
  z-index: 10000;
}

.ls-scroll-menu {
  position: relative;
  display: flex;
}

.ls-line {
  margin-top: 8px;
  width: 80px;
  border: 2px solid #3553e8;
  transition: transform 0.5s linear;
}

@media screen and (max-width: 800px) {
  .ls-scroll-container {
    padding: 40px 20px;
  }
}

@media screen and (max-width: 610px) {
  .seat {
    height: 93px;
  }
  .ls-scroll-container {
    padding: 30px 12px;
  }
  .ls-line {
    width: 70px;
    transform: translateX(6px);
  }
}
</style>