<template>
  <div class="ls-card" v-enterTransit>
    <div class="ls-card-img-comtainer imgContainer" :style="reverseStyle">
      <slot name="img"></slot>
      <div class="ls-card-sub">
        <div class="ls-card-move-title">{{ title }}</div>
      </div>
    </div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "ls-card",
  props: {
    title: String,
    reverse: Boolean,
  },
  data() {
    return {
      reverseStyle: {
        order: this.reverse ? 1 : 0,
      },
    };
  },
  mounted() {
    this.resize();
    window.addEventListener("resize", this.resize);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resize);
  },
  methods: {
    resize() {
      // 当视口改变，监听是否小于800，如果小于那就不能reverse
      if (window.innerWidth <= 800) {
        this.reverseStyle.order = 0;
      } else {
        this.reverseStyle.order = this.reverse ? 1 : 0;
      }
    },
  },
};
</script>

<style lang="less">
.ls-card {
  display: flex;
  margin: 70px 20px 0px 20px;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  transition: all 0.5s;
}

.ls-card-img-comtainer {
  position: relative;
  flex: 1;
  height: 416px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.ls-card-sub {
  display: none;
}

@media screen and (max-width: 800px) {
  .ls-card {
    margin: 30px 5px 0 5px;
    width: 335px;
    height: 459px;
    flex-direction: column;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  }
  .ls-card-img-comtainer {
    width: 335px;
    height: 249px;
  }
  .ls-card-sub {
    display: block;
  }
  .ls-card-move-title {
    color: #031436;
    font-size: 16px;
  }
}
</style>