<template>
  <div
    class="ls-lazy-container imgContainer"
    :style="{ width, height }"
    ref="container"
    :data-src="src"
  >
    <a-icon type="loading" spin/>
  </div>
</template>

<script>
import { imgContiners, ImageLazyLoad } from "../utils";


export default {
  name: "ls-lazyLoad",
  
  props: {
    width: String,
    height: String,
    src: {
      type: String,
    },
  },
  
  data() {
    return {
      lazyImage: null,
    };
  },

  mounted() {
    this.lazyImage = new ImageLazyLoad();
    imgContiners.add(this.$refs.container);
    this.lazyImage.observe()
  }, 

  watch: {
    $route() {
      this.lazyImage.observe();
    },
  },
};
</script>

<style lang="less">
.ls-lazy-container {
  position: relative;
}

.ls-lazy-container {
  img {
    opacity: 0;
  }
}

.ls-lazy-container .load {
  opacity: 1;
  transition: all 0.5s;
}

.load {
  .anticon {
    display: none;
  }
}

.anticon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 50px;
  color: #3553e8;
}
</style>