
function scroll(el) {
    return function proxy() {
        let { top } = el.getBoundingClientRect();
        if (top < window.innerHeight) {
            el.classList.remove('enterTramsit')
            el.style.transition = '';
            window.removeEventListener('scroll', proxy)
        }
    }
}

const enterTransit = {  
    inserted(el, dirs, vnode) {
        el.classList.add('enterTramsit')
        el.style.transition = 'none';
        scroll(el)();
        window.addEventListener('scroll', scroll(el))
    },
    componentUpdated(el) {
        scroll(el)();
    }
}

export default enterTransit;