<template>
  <div class="ls-card-mate" :style="borderStyle">
    <div class="ls-card-title">{{ $parent.title }}</div>
    <div class="ls-card-dirs">
      <slot name="dirs"></slot>
    </div>

    <div class="ls-card-button">
      <router-link to="/"> 了解更多</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "ls-card-mete",
  data() {
    return {
      borderStyle: {
        border: "",
        borderLeft: "",
        borderRight: "",
      },
    };
  },
  mounted() {
    this.resize();
    window.addEventListener("resize", this.resize);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resize);
  },
  methods: {
    resize() {
      if (this.$parent.reverse) {
        if (window.innerWidth <= 800) {
          this.borderStyle.border = "none";
          this.borderStyle.borderRight = "none";
          this.borderStyle.borderLeft = "none";
        } else {
          this.borderStyle.border = "";
          this.borderStyle.borderRight = 0;
          this.borderStyle.borderLeft = "1px solid #707070";
        }
      }
    },
  },
};
</script>

<style lang="less" scoped>
.ls-card-mate {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 30px;
  flex: 1;
  height: 416px;
  border: 1px solid #707070;
  border-left: 0px;
}
.ls-card-title {
  font-size: 26px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.ls-card-dirs {
  margin-top: 20px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5;
  overflow: hidden;
  font-size: 14px;
  line-height: 30px;
}

.ls-card-button {
  margin-top: 10px;
  a {
    color: #3553e8;
    cursor: pointer;
  }
}

@media screen and(max-width: 800px) {
  .ls-card-mate {
    padding: 20px;
    width: 335px;
    border: none;
  }
  .ls-card-title {
    padding-bottom: 10px;
    font-weight: 600;
    font-size: 16px;
  }
  .ls-card-dirs {
    margin-top: 0;
    -webkit-line-clamp: 6;
    height: 177px;
    font-size: 10px;
  }
  .ls-card-button {
    display: none;
  }
}
</style>