<template>
  <div class="ls-tabs-item" @click="moveLine">
    <router-link tag="span" :to="path">
      <slot> </slot>
    </router-link>
  </div>
</template>

<script>
export default {
  name: "ls-tabs-item",
  props: {
    path: String,
  },
  mounted() {
    this.checkLine(true);
    window.addEventListener("resize", this.checkLine);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.checkLine);
  },
  methods: {
    moveLine(e) {
      if (e.target.tagName === "SPAN") {
        let x = e.target.offsetLeft;
        let line = this.$parent.$refs.line;
        line.style.transform = `translateX(${x - 12}px)`;
      }
    },
    checkLine(flag) {
      if (this.$el.pathname === this.$route.path) {
        let el = this.$el.firstElementChild;
        let line = this.$parent.$refs.line;
        if (flag) {
          line.style.transition = "none";
        }
        line.style.transform = `translateX(${el.offsetLeft - 12}px)`;
        if (flag) {
          line.offsetTop;
          line.style.transition = "transform 0.5s linear";
        }
      }
    },
  },
  watch: {
    $route() {
      this.checkLine();
    },
  },
};
</script>

<style lang="less" scoped>
.ls-tabs-item {
  padding: 8px 12px;
  font-size: 14px;
  span {
    cursor: pointer;
  }
}
</style>