import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    meta: {
      title: '首页'
    },
    component: Home
  },
  {
    name: 'Service',
    path: '/service/:id',
    meta: {
      title: '业务中心'
    },
    component: () => import(/* webpackChunkName: "Service" */ '../views/Service.vue'),
  },
  {
    name: 'Education',
    path: '/education',
    meta: {
      title: '教育体系'
    },
    redirect: '/education/vocational-education',
    component: () => import(/* webpackChunkName: "Education" */ '../views/Education.vue'),
    children: [
      {
        path: 'vocational-education',
        meta: {
          title: '职业教育'
        },
        component: () => import(/* webpackChunkName: "Service" */ '@education/vocational.vue')
      },
      {
        path: 'education-training',
        meta: {
          title: '教育培训'
        },
        component: () => import(/* webpackChunkName: "Service" */ '@education/training.vue')
      }
    ]
  },
  {
    name: 'Cooperate',
    path: '/cooperate',
    meta: {
      title: '携手合作'
    },
    redirect: '/cooperate/cooperate-colleges',
    component: () => import(/* webpackChunkName: "Cooperate" */ '../views/Cooperate.vue'),
    children: [
      {
        path: 'cooperate-colleges',
        meta: {
          title: '合作院校'
        },
        component: () => import(/* webpackChunkName: "Colleges" */ '@cooperate/colleges.vue')
      },
      {
        path: 'school-enterprise-cooperate',
        meta: {
          title: '校企合作'
        },
        component: () => import(/* webpackChunkName: "SchoolEnterprise" */ '@cooperate/schoolEnterprise.vue')
      }
    ]
  },
  {
    name: 'News',
    path: '/news/:id',
    meta: {
      title: '新闻资讯'
    },
    component: () => import(/* webpackChunkName: "News" */ '../views/News.vue'),
  },
  {
    name: 'About',
    path: '/about/:id',
    meta: {
      title: '关于我们'
    },
    component: () => import(/* webpackChunkName: "About" */ '../views/About.vue'),
  },
  {
    name: 'Details',
    path: '/details/:id',
    meta: {
      title: '详情页'
    },
    component: () => import(/* webpackChunkName: "About" */ '../views/Details.vue'),
  }
]

export const navList = [
  {
    til: "业务中心",
    path: "/service",
  },
  {
    til: "教育体系",
    path: "/education",
  },
  {
    til: "携手合作",
    path: "/cooperate",
  },
  {
    til: "新闻资讯",
    path: "/news",
  },
  {
    til: "关于我们",
    path: "/about",
  },
];

export const subList = [
  [
    {
      title: "业务板块",
      path: "/service/service-center",
    },
    {
      title: "院校开发",
      path: "/service/college-development",
    },
    {
      title: "服务模式",
      path: "/service/service-mode",
    },
    {
      title: "拓展业务",
      path: "/service/expand-service",
    },
  ],
  [
    {
      title: "职业教育",
      path: "/education/vocational-education",
    },
    {
      title: "教育培训",
      path: "/education/education-training",
    },
  ],
  [
    {
      title: "合作院校",
      path: "/cooperate/cooperate-colleges",
    },
    {
      title: "校企合作",
      path: "/cooperate/school-enterprise-cooperate",
    },
  ],
  [
    {
      title: "公司新闻",
      path: "/news/company-news",
    },
    {
      title: "行业动态",
      path: "/news/industry-news",
    },
  ],
  [
    {
      title: "公司简介",
      path: "/about/company-profile",
    },
    {
      title: "发展理念",
      path: "/about/dev-concept",
    },
    {
      title: "联系我们",
      path: "/about/contact-us",
    },
  ],
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
})

router.beforeEach((to, from, next) => {
  if (/^\/(service|news|about)$/.test(to.path)) {
    let list = subList.find(item => item[0].path.startsWith(to.path))
    next(list[0].path);
  }
  next();
})

export default router
