<template>
  <div class="seat">
    <div class="navBox" v-subOutside>
      <div class="base">
        <div class="navList">
          <!-- LOGO -->
          <div class="logo">
            <router-link tag="h1" to="/">
              <img src="@img/5f50b42768a4a2.30275879.png" alt="logo" />
            </router-link>
          </div>
          <!-- 点击按钮 -->
          <div class="menuBut" @click="cut">
            <img src="/img/Button.png" alt="" />
          </div>
          <!-- 菜单栏 -->
          <div class="menuBox" ref="menuBox">
            <div class="menu" @mouseover="emerge" @mouseout="narrow">
              <div v-for="(item, key) in navList" :key="key">
                <router-link
                  :to="item.path"
                  class="til"
                  :pathname.prop="item.path"
                >
                  {{ item.til }}
                  <span class="line" ref="line"></span>
                </router-link>
              </div>
            </div>
          </div>
        </div>
        <!-- 二级导航栏 -->
        <transition name="subNav">
          <div class="subNavBox" v-show="subShow">
            <div class="subNavList">
              <div class="empty"></div>
              <div class="subMenn">
                <div v-for="(items, key) in subList" :key="key">
                  <div class="subItem" v-for="item in items" :key="item.title">
                    <router-link class="til" :to="item.path">
                      {{ item.title }}
                    </router-link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>


<script>
import { navList, subList } from "../../router";

const pathMap = {
  service: 0,
  education: 1,
  cooperate: 2,
  news: 3,
  about: 4,
};

export default {
  data() {
    return {
      show: false,
      subShow: false,
      isAfterEnter: false,
      isAfterLeave: true,
      navList:Object.freeze(navList),
      subList: Object.freeze(subList),
    };
  },
  mounted() {
    let menuBox = this.$refs["menuBox"];
    menuBox.resize = () => {
      if (window.innerWidth > 800) {
        // 为了能让更大屏的菜单文字显示
        menuBox.style.height = "auto";
        this.show = true;
        // 视口改变，清除全局的click事件
        document.documentElement.removeEventListener("click", this.stowMenu);
      } else {
        // 如果小于800 一律隐藏菜单，需要重新点击按钮显示
        menuBox.style.height = 0;
        this.show = false;
      }
    };
    window.addEventListener("resize", menuBox.resize);
  },
  methods: {
    emerge(e) {
      let el = e.target;
      // 事件委托
      if (el.tagName === "A") {
        if (this.$route.path !== el.pathname) {
          let lines = this.$refs["line"];
          for (let key in pathMap) {
            let pathname = el.pathname.slice(1); // 获取当前按钮的路径就知道现在触碰的是哪个按钮
            pathname.indexOf(key)
              ? null
              : lines[pathMap[key]].classList.add("hover");
          }
        }
      }
    },
    narrow(e) {
      let el = e.target;
      if (el.tagName === "A") {
        let lines = this.$refs["line"];
        for (let key in pathMap) {
          let pathname = el.pathname.slice(1); // 获取当前按钮的路径就知道现在触碰的是哪个按钮
          pathname.indexOf(key)
            ? null
            : lines[pathMap[key]].classList.remove("hover");
        }
      }
    },
    cut() {
      let menuBox = this.$refs["menuBox"];
      if (this.show) {
        this.show = false;
        menuBox.style.height = 0;
      } else {
        this.show = true;
        menuBox.style.height = `310px`;
        setTimeout(() => {
          // 如果我们改变视口尺寸，之后再回来点击菜单按钮，就会触发上一次的click事件（导致异常）
          // 然后这一次的又是一个定时器click，又等到下一次如果下次触发的还是点击菜单按钮，就触发上次的click，再在全局绑定一个click
          document.documentElement.addEventListener("click", this.stowMenu);
        });
      }
    },
    stowMenu() {
      this.show = false;
      this.$refs["menuBox"].style.height = 0;
      document.documentElement.removeEventListener("click", this.stowMenu);
    },
  },
  directives: {
    subOutside: {
      bind(el, dirs, vnode) {
        let timer = null;
        let vm = vnode.context;
        // 进来的是时候清除离开时的定时器
        let enter = () => {
          if (window.innerWidth > 800) {
            clearTimeout(timer);
            vm.subShow = true;
          }
        };
        let leave = () => {
          if (window.innerWidth > 800) {
            clearTimeout(timer);
            timer = setTimeout(() => {
              vm.subShow = false;
              timer = null;
            }, 400);
          }
        };
        el.addEventListener("mouseenter", enter);
        el.addEventListener("mouseleave", leave);
      },
    },
  },
};
</script>

<style lang="less" scoped>
.subNav-enter {
  height: 0;
}
.subNav-enter-to {
  height: 216px;
  transition: height 0.3s linear;
}
.subNav-leave {
  height: 216px;
}
.subNav-leave-to {
  height: 0;
  transition: height 0.3s linear;
}

.seat {
  /* 占位盒子，防止因为固定定位导致位置丢失 */
  width: 100%;
  height: 72px;
}
.navBox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 72px;
  background-color: #ffffff;
  box-shadow: 0px 0px 5px #99999950;
  transition: all 0.5s;
  z-index: 1000000;
}
.navList {
  display: flex;
  height: 72px;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
}
.logo {
  margin: 16px 20px;
  width: 109px;
  height: 40px;
  cursor: pointer;
  & img {
    width: 100%;
    height: 100%;
  }
}
.menuBut {
  display: none;
  cursor: pointer;
}
.subNavBox {
  overflow: hidden;
}
.subNavList {
  display: flex;
  justify-content: space-between;
  background-color: #ffffff;
  height: 216px;
  .subMenn {
    display: flex;
    .subItem {
      margin-bottom: 20px;
    }
    .til {
      font-size: 16px;
      margin-right: 30px;
      color: #555555;
    }
    .til:hover {
      color: #3553e8;
    }
  }
}
.menu {
  display: flex;
  justify-content: center;
  font-size: 16px;
  text-align: center;
  .til {
    position: relative;
    margin-right: 30px;
    text-align: center;
    color: #000000;
    font-weight: 400;
    .line {
      position: absolute;
      width: 60px;
      height: 4px;
      left: 2px;
      bottom: -8px;
      border-radius: 8px;
      background-color: #3553e8;
      transform: rotateY(90deg);
      transition: transform 0.5s linear;
    }
    .hover {
      transform: rotateY(0deg);
      transition: transform 0.5s; /* 使用转换中的角度转换 */
    }
  }
  .router-link-active {
    .line {
      transform: none;
      transition: none;
    }
  }
}

@media screen and(max-width:800px) {
  .menuBut {
    display: block;
    margin-right: 20px;
  }
  .navList {
    max-width: 780px;
  }
  .menuBox {
    position: absolute;
    top: 70px;
    width: 100%;
    height: 0px;
    transition: height 0.2s linear;
    background-color: #ffffff;
    overflow: hidden;
    z-index: 1;
    .menu {
      display: flex;
      height: 310px;
      flex-direction: column;
      justify-content: space-around;
    }
    .til {
      margin: 0px;
      .line {
        display: none;
      }
    }
  }
  .subNavList {
    display: none;
  }
  .til.router-link-active {
    color: #3553e8;
  }
  .til:hover {
    color: #3553e8;
  }
}

</style>