
/**
 * 管理图片
 */
const imgContiners = new Set();


class ImageLazyLoad {
    constructor() {
        // 记录每个图片的容器
        this.container = null;
        this.fn = null;
        this.init();
    }

    init() {
        this.observe();
        this.fn = () => this.observe();
        window.addEventListener('scroll', this.fn);
    }

    observe() {
        imgContiners.forEach(container => {
            if (document.documentElement.contains(container)) {
                let { top } = container.getBoundingClientRect();
                if (top < window.innerHeight) {
                    this.container = container;
                    this.loading();
                    imgContiners.delete(container);
                }
            }
        });
    }

    loading() {
        let container = this.container;
        let src = container.getAttribute('data-src');
        let img = new Image();
        let timer = setTimeout(() => {
            img.src = src;
            clearTimeout(timer);
            timer = null;
        }, 500)
        img.onload = () => this.load(img, container);
        img.onerror = () => this.error(img, container);
    }

    load(img, container) {
        container.append(img);
        container.removeAttribute('data-src');
        container.offsetTop;
        container.classList.add('load')
        img.className = 'load imgHover';
        window.removeEventListener('scroll', this.fn);
    }

    error(img, container) {
        window.removeEventListener('scroll', this.fn);
    }
}


function pageLoad() {

}

export {
    imgContiners,
    ImageLazyLoad
}