<template>
  <div class="seat">
    <div class="ls-tabs" ref="tabs">
      <div class="ls-tabs-container">
        <slot></slot>
      </div>
      <div class="ls-line" ref="line"></div>
    </div>
  </div>
</template>

<script>
import { getOffset } from "@utils";

export default {
  name: "ls-tabs",
  data() {
    return {
      tabs: null,
      tabsT: null,
    };
  },
  mounted() {
    this.fixedContainer();
  },
  methods: {
    scroll() {
      let winT = document.documentElement.scrollTop;
      if (winT >= this.tabsT - 72) {
        this.tabs.style.position = "fixed";
        this.tabs.style.top = `${72}px`;
      } else {
        this.tabs.style.position = "relative";
        this.tabs.style.top = ``;
      }
    },
    fixedContainer() {
      this.tabs = this.$refs.tabs;
      this.tabsT = getOffset(this.tabs).t;
      window.addEventListener("scroll", this.scroll);
    },
  },
};
</script>

<style lang="less">
.seat {
  position: relative;
  width: 100%;
  height: 109px;
}

.ls-tabs {
  width: 100%;
  padding: 30px 40px;
  background-color: #ffffff;
  z-index: 10000;
}

.ls-tabs-container {
  position: relative;
  display: flex;
}

.ls-line {
  width: 80px;
  border: 2px solid #3553e8;
}

@media screen and (max-width: 375px) {
  .ls-tabs-container {
    justify-content: center;
  }
}
</style>